
body {
  font-family: 'PT Sans', sans-serif;
  background: #fff;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.5))), url(/static/img/parchment.6cbdd66.jpg) no-repeat fixed center;
  background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url(/static/img/parchment.6cbdd66.jpg) no-repeat fixed center;
  background-size: cover;
}
.dashboard-row {
  min-height: 35vh;
  border-top: 2px solid #000;
}
.dashboard-main {
  min-height: 30vh;
}
.empty-message {
  margin-bottom: 30px;
}
.search-bar {
  margin-bottom: 40px;
}
.keep {
  border: 1px solid #404040;
  background: #fff;
}
.main-title {
  font-size: 4em;
  margin: 40px 0 40px;
}
.secondary-title {
  font-size: 3em;
  margin: 40px 0 40px;
}
.btn-primary {
  background: #737373;
  border: 1px solid #737373;
}
.btn-alt {
  background: #ffcc00;
  border: 1px solid #ffcc00;
}
.btn-alt-2 {
  background: #00ffff;
  border: 1px solid #00ffff;
}
.btn-primary:hover,
.btn-primary:active,
.btn-primary:focus {
  background: #404040;
  border: 1px solid #404040;
}
.btn-alt:hover,
.btn-alt:active,
.btn-alt:focus {
  background: #e6b800;
  border: 1px solid #e6b800;
}
.btn-alt-2:hover,
.btn-alt-2:active,
.btn-alt-2:focus {
  background: #00e6e6;
  border: 1px solid #00e6e6;
}
.image-wrapper {
  position: relative;
  width: 100%;
}
.image-wrapper:hover .keep-image {
  opacity: 0.3;
}
.image-wrapper:hover .overlay-content {
  opacity: 1;
}
.keep-image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 1%;
  border: 1px solid #404040;
  margin-bottom: 15px;
}
.keep-large-view {
  margin-bottom: 20px;
}
.overlay-content {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}
.glyphicon {
  font-size: 1.5em;
}
.custom-icon,
.counter-label {
  font-size: 1.4em;
}
.counter {
  margin: 0 5px;
}
.btn-icon {
  width: 45px;
  height: 40px;
  border-radius: 20%;
}
.add-button {
  width: 42px;
  height: 40px;
  border-radius: 100%;
}
.main-font {
  font-family: 'Modern Antiqua', cursive;
}
.btn-default {
  width: 100%;
}
.navbar ul {
  list-style-type: none;
}
.right-btn {
  margin: 0 10px 0 2px;
}
@media (max-width: 2000px) {
.navbar-header {
    float: none;
}
.navbar-toggle {
    display: block;
}
.navbar-nav {
    float: none!important;
    margin: 7.5px -15px;
}
.navbar-nav>li {
    float: none;
}
.navbar-nav>li>a {
    padding-top: 10px;
    padding-bottom: 10px;
}
.state {
    width: 50%
}
}






















































































































































































